export default {
  name: "videoitemobject",
  created() {
    if (this.topic_data) {
      console.log('call videoitemobject created ');
      this.online_video = this.topic_data.video_info.online_videos[0];
    }
  },
  methods: {
    get_preview_url: function () {
      let preview_mp4_url = "https://" + this.$config.space_confs[this.online_video.space_name].static_hosts[0] + "/videos/" + this.online_video.hash_id + "/preview.mp4";
      return preview_mp4_url;
    }
    ,
    get_cover_image_url: function () {
      let cover_image_url = this.generate_small_cover_url(this.online_video.space_name, this.online_video.hash_id);
      return cover_image_url;
    }
    ,
    get_big_cover_image_url: function () {
      let cover_image_url = this.generate_cover_url(this.online_video.space_name, this.online_video.hash_id);
      return cover_image_url;
    }
    ,
    on_cover_error: function (e) {
      console.log('[on_cover_error]:', e);
    }
    ,
    mouseleave: function (e) {
      console.log('[mouseleave]:', e);
      this.preivew_url = undefined;
    }
    ,
    mouseover: function (e) {
      this.preivew_url = this.get_preview_url();
      console.log('[mouseover]:', this.preivew_url);

    }
  }
}
