export default {
  name: "sidebar",
  data() {
    return {
      show_siderbar: false
    }
  },
  methods: {
    on_click_menu: function () {
      this.show_siderbar = false;
      console.log('on_click_menu');
    },
    show: function () {
      this.$nextTick(() => {
        this.show_siderbar = true;
      });
    }
  }, mounted() {
    document.addEventListener('click', e => {
      if (!this.$el.contains(e.target)) {
        console.log('点击sidebar其它地方')
        this.show_siderbar = false
      }
    })
  }
}
