import search_bar from '@/components/common/search.bar'

export default {
  name: "mainpage",
  components: {
    search_bar,
    mobile_menu: () => import("@/components/common/mobile.menu"),
    user__dialog: () => import("@/components/user/user.dialog")
  },
  data() {
    return {
      header_height: 0,
      show_user_dialog: false

    }
  },
  created() {
    this.update_header_height();
  },
  methods: {
    on_user_center_click: function () {
      if (!this.$store.state.hasLogin) {
        this.show_user_dialog = false;
        this.$nextTick(() => {
          this.show_user_dialog = true;
          this.$forceUpdate();
        });
        return;
      }
      this.$router.push('/user/center')
    },
    update_header_height: function () {
      this.$nextTick(() => {
        if (this.$refs.header) {
          console.log('this.$refs.header:', this.$refs.header);
          if (this.$refs.header) {
            console.log('this.$refs.header_menu.offsetHeight:', this.$refs.header.offsetHeight);
            this.header_height = this.$refs.header.offsetHeight;
          }
        }
      });
    },
    on_next_page: function () {

    },
    on_last_page: function () {

    },
    on_menu_click: function () {

    }
  }
}
